export const dateFormatWithMonthName = {
  parse: { dateInput: { month: 'short', year: 'numeric', day: 'numeric' } },
  display: {
    dateInput: 'input',
    monthYearLabel: { year: 'numeric', month: 'short' },
    dateA11yLabel: { year: 'numeric', month: 'short' },
    monthYearA11yLabel: { year: 'numeric', month: 'short' }
  }
};

export function standardFormattedDate(
  date: any,
  shortMonthFormat: boolean = false
): string {
  date = typeof date === 'object' ? date : new Date(date);
  let option: any;
  if (shortMonthFormat) {
    option = {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    };
  } else {
    option = {
      year: 'numeric',
      month: 'numeric',
      day: 'numeric'
    };
  }
  return date.toLocaleDateString('en-IN', option).toUpperCase();
}

export function standardFormattedDateTime(date: any): string {
  date = typeof date === 'object' ? date : new Date(date);

  return date.toLocaleTimeString('en-IN', { hour12: false });
}

export function standardFormattedDateNoYear(date: any, shortMonthFormat: boolean = false): string {
  date = typeof date === 'object' ? date : new Date(date);
  let option: any;
  if (shortMonthFormat) {
    option = {
      month: 'short',
      day: 'numeric'
    };
  } else {
    option = {
      month: 'numeric',
      day: 'numeric'
    };
  }
  return date.toLocaleDateString('en-IN', option);
}
